import React from 'react'
import App, { Container } from 'next/app'
import './main.css'
import { Provider } from 'redux-zero/react'
import store from '../states/store'
import {connect} from 'redux-zero/react';

import firebase from 'firebase/app'
import 'firebase/auth'
import firebaseConf from '../conf/firebase'
import AddNavigation from '../widgets/addNavigation'
import Button from '../components/button'
import Loading from '../widgets/loading'

import fn from '../states/fn';

const mapToProps = ({ s , d}) => ({ s, d });

class Default extends App {
    constructor(props) {
        super(props)
    }

    static async getInitialProps({ Component, router, ctx }) {
        let pageProps = {}
        if (Component.getInitialProps) {
            pageProps = await Component.getInitialProps(ctx)
        }
        return { pageProps }
    }

    componentDidMount() {
        firebase.auth().onAuthStateChanged((user) => {
            if (user) {
                fn().login(store)
            }else{
                Router.push('/login')
            }
        })
    }
    componentDidUpdate() {
        console.log(store)
    }
    render () {
        const { Component, pageProps } = this.props
        console.log(store.getState())
        if(store.getState().s.login) {
            return (
                <Provider store={store}>
                    <Container>
                        <Component {...pageProps} />
                        <AddNavigation />
                        <Button type="add" />
                    </Container>
                </Provider>
            )
        } else {
            return <Loading {...pageProps}/>
        }
    }
}

export default connect(mapToProps, fn)(Default)
